import { Grid, Tag, Space, Input, Card, Form, Button } from 'antd-mobile';
import { useState } from 'react';
import './ElectricityStyle.scss';

function WaterPage() {
    const [TotalPrice, setTotalPrice] = useState(0);
    const [South, setSouth] = useState({});
    const [North, setNorth] = useState({});

    const sumbit = () => {

        const SouthData = South;//
        const NorthData = North;//


        let NDosage = SouthData.newMonth - SouthData.oldMonth; //
        let BDosage = NorthData.newMonth - NorthData.oldMonth; //
        let Total = NDosage + BDosage; //
        let Npercentage = Math.round(NDosage / Total * 10000) / 100.00 + '%'; //
        let Bpercentage = Math.round(BDosage / Total * 10000) / 100.00 + '%'; //
        let NPrice = (TotalPrice / Total) * NDosage; //
        let BPrice = (TotalPrice / Total) * BDosage; //

        setSouth({ ...South, Dosage: NDosage, percentage: Npercentage, Price: NPrice.toFixed(2) });
        setNorth({ ...North, Dosage: BDosage, percentage: Bpercentage, Price: BPrice.toFixed(2) });
        console.log('South', South);
    }

    return (
        <>
            <div className='Input_style'>
                {/* <Input placeholder=' ' clearable type='number' onChange={(val) => { setTotalPrice(val) }} /> */}
                <Form layout='horizontal'>
                    <Form.Item label=''>
                        <Input placeholder=' ' clearable type='number' onChange={(val) => { setTotalPrice(val) }} />
                    </Form.Item>
                </Form>
            </div>
            <div className='tab_view'>
                <Grid columns={2} gap={8}>
                    <Grid.Item>
                        <Card title={
                            <Space>
                                <Tag color='primary' round={true} className='TagStyle'></Tag>
                            </Space>}>
                            <>
                                <Form layout='horizontal'>
                                    <Form.Item label=''>
                                        <Input placeholder='' type='number' onChange={
                                            (val) => {
                                                setSouth({ ...South, oldMonth: Number(val) })
                                            }} />
                                    </Form.Item>
                                    <Form.Item label=''>
                                        <Input placeholder='' type='number' onChange={
                                            (val) => {
                                                setSouth({ ...South, newMonth: Number(val) })
                                            }} />
                                    </Form.Item>
                                </Form>
                                <div className='info_style'>
                                    <p>{South.Dosage}</p>
                                    <p>{South.percentage}</p>
                                    <p>{South.Price}</p>
                                </div>
                            </>
                        </Card>

                    </Grid.Item>

                    <Grid.Item>
                        <Card title={
                            <Space>
                                <Tag color='success' round={true} className='TagStyle'></Tag>
                            </Space>
                        }>
                            <>
                                <Form layout='horizontal'>
                                    <Form.Item label=''>
                                        <Input placeholder='' type='number' onChange={
                                            (val) => {
                                                setNorth({ ...North, oldMonth: Number(val) })
                                            }} />
                                    </Form.Item>
                                    <Form.Item label=''>
                                        <Input placeholder='' type='number' onChange={
                                            (val) => {
                                                setNorth({ ...North, newMonth: Number(val) })
                                            }} />
                                    </Form.Item>
                                </Form>
                                <div className='info_style'>
                                    <p>{North.Dosage}</p>
                                    <p>{North.percentage}</p>
                                    <p>{North.Price}</p>
                                </div>
                            </>
                        </Card>

                    </Grid.Item>
                </Grid>
            </div>
            <div className='btn_style'>
                <Button size='middle' color='primary' onClick={sumbit}>
                    
                </Button>
            </div>
        </>
    );
}
export default WaterPage;